import { _decorator, Component, Node, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('logoAnim')
export class logoAnim extends Component {

    start() {

    	this.showEffect();
    }

    update(deltaTime: number) {
        
    }

    showEffect() {
    	console.log('showEffect = ', this.node);
        cc.tween(this.node)
        // Delay 1s
        .delay(1)
        .to(1, { scale: 2 })
        // Another delay of 1s
        .delay(1)
        .to(1, { scale: 3 })
        .start();
    }
}

